<template>
    <v-toolbar 
        
        class='custom-nav' 
        grey darken-2
        scroll-off-screen
        dense
    >


        <v-spacer> </v-spacer>
        <v-toolbar-items>
            <template v-for='(item,k) in path'> 
                <v-btn flat 
                    exact
                    :to='item.path'
                    v-if='filterNav(item)'
                    :key="k"
                > {{item.name ? item.name :  item.altName}} </v-btn>
            </template>
        </v-toolbar-items>
        
        <div v-if='$vuetify.breakpoint.smAndUp'> 
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <img src='arya.png'
                style='
                height: 34px;
                width: 120px;
                padding: 0px 0px 0px 0px;
                '
            /> 
            
        </div>
    </v-toolbar>
</template>

<script>
export default {

    name : 'Navigation',
    methods: {
        filterNav(item){
            return !item.hidden  ? true : false
        }
    },
    computed: {
        path(){
            
            return this.$router.options.routes
        }
    }

}
</script>

