import Vue from 'vue'
import App from './app.vue'
//import Vuetify from 'vuetify'
//import Anime from 'vue-animejs'
import Transitions from 'vue2-transitions'
import Router from 'vue-router'

import './utilities/media-queries/mq'


import Lightbox from 'vue-easy-lightbox'

import './plugins/vuetify'
 
Vue.use(Lightbox)



import Revealer from './components/helpers/revealer'

Vue.component('revealer',Revealer)

import anime from 'animejs/lib/anime.min.js'
Vue.prototype.$anime = anime;

window.IntersectionObserver ? true : require('intersection-observer')

import VueObserveVisibility from 'vue-observe-visibility'
Vue.use(VueObserveVisibility)

//Vue.use(Anime)
Vue.use(Transitions)
Vue.use(Router)

// fixes 

const hacks = require('viewport-units-buggyfill/viewport-units-buggyfill.hacks')
require('viewport-units-buggyfill').init({
  hacks: hacks
})


//routes 

import front from './components/front2.vue'

const base2 = () => import('./components/products2/base.vue')
const categories2 = () => import('./components/products2/categories')
const products2 = () => import('./components/products2/products')
const aboutus = () => import('./components/aboutus')

import pg from './components/pg'
import container from './components/helpers/container.vue'

const product = () =>  import('./components/products2/product')
const innerbase = () => import('./components/products2/base2')

const gallery = () => import('./components/gallery')

Vue.component('container',container)

import Gallery from 'vue-gallery';
Vue.component('gallery',()=>Gallery)

//import('../external/fullpage')
// import('../external/scrolloverflow')

import 'fullpage.js/vendors/scrolloverflow' // Optional. When using scrollOverflow:true
//import './fullpage.scrollHorizontally.min' // Optional. When using fullpage extensions
import VueFullPage from 'vue-fullpage.js'

Vue.use(VueFullPage);


let checkImg = document.createElement('img')
!('sizes' in checkImg) ? require('picturefill') : true



let routes = [
  {
    path: '/',
    component: front,
    name: 'Home'
  },
  {
    path: '/category',   
    component: base2,
    altName: 'PRODUCTS',
    children: [
      {
        path: '/',
        component: categories2,
        name: 'Products-2'
      
      },
      {
        
        path: ':id-:slug',
        component: innerbase,
        children: [
          {
            path: '/',
            component: products2,
            name: 'product-lst'
          },
          {
            path: 'product/:pid',
            component: product,
            name: 'product'
          }
        ]
      }
    ]
  },

  {
    path: '/aboutus',
    component: aboutus,
    name: 'About Us'
  },
  {
    path: '/gallery',
    component: gallery,
    name: 'Gallery'
  },
  {
    path: '/product',
    component: products2,
    name: 'Product',
    hidden: true
  },
  
  {
    hidden: true,
    path: '/pg',
    component: pg
  }
]

/*   ||   'http://164.52.192.188:8181'  */;

Vue.config.productionTip = false
Vue.prototype.$axios = require('axios')

Vue.prototype.$axios.defaults.headers = { 'Content-Type': 'application/json' }

Vue.prototype.$qs = require('qs')

/*let appPath = 'http://localhost'

if(process.env.production)
  appPath = process.env.https ? 'https://164.52.192.188:8181' : 'http://164.52.192.188:8181'

*/

Vue.prototype.$host  = process.env.BASE_URL

Vue.prototype.$axios.defaults.baseURL = `${Vue.prototype.$host}api/v1`;


//console.log(`Origin is ${Vue.$host} AND ${Vue.prototype.$host}`)


// css

Vue.filter('url', (val) => {

  if(val[0] != '/')
    val = `/${val}`
  return `${Vue.prototype.$host}${val}`
})


import 'material-design-icons-iconfont/dist/material-design-icons.css'


// use


new Vue({
  render: h => h(App),
  router: new Router({
    routes: routes,
    mode: 'history',
    //vuetify
  })
}).$mount('#app')

// /import './cache/cache'
import './styles/main.css'


